<template>
  <el-dialog
    title=""
    :visible.sync="changePassDialog"
    :before-close="hidePassDialog"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    width="35%"
  >
    <el-form status-icon :model="form" :rules="rules"  ref="ruleForm" :label-position="labelPosition">
      <el-form-item label="" :label-width="formLabelWidth" prop="name">
        <el-input placeholder="" v-model="form.name" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth" prop="pass">
        <el-input placeholder="" v-model="form.pass" maxlength="16" show-password autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth" prop="checkPass">
        <el-input placeholder="" v-model="form.checkPass" maxlength="16" show-password autocomplete="off"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="clearPassDialog('ruleForm')"> </el-button>
      <el-button @click="resetForm('ruleForm')"> </el-button>
      <el-button type="primary" @click="submitForm('ruleForm') "> </el-button>
    </div>
  </el-dialog>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
     var checkName = (rule, value, callback) => {
        if (!value) {
          return callback(new Error(''));
        }
        setTimeout(() => {
         console.log(value.length)
          if (value.length > 10 || value.length < 2) {
            callback(new Error(''));
          } else {
            callback();
          }
        }, 1000);
      };
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          if (this.form.checkPass !== '') {
            this.$refs.ruleForm.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.form.pass) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };
    return {
      // changePassDialog:false,
      labelPosition: 'right',
      form: {
        name: "",
        pass: "",
        checkPass: ""
      },
      rules: {
          pass: [
            { validator: validatePass, trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ],
          name: [
            { validator: checkName, trigger: 'blur' }
          ]
      },
      formLabelWidth: "80px"
    };
  },
  computed: {
    ...mapState ({
      changePassDialog: (state) => state.form.changePassDialog
    })
  },
  methods: {
    hidePassDialog () {
      this.$store.dispatch('changeChangePass', false);
    },
    clearPassDialog (formName) {
      this.$store.dispatch('changeChangePass', false);
      // 
      this.$refs[formName].resetFields();
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log('submit', this.form);
          setTimeout(() => {
            this.$store.dispatch('changeChangePass', false);
            this.$refs[formName].resetFields();
          }, 1000)
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      console.log(this.$refs[formName]);
      this.$refs[formName].resetFields();
    }
  }
};
</script>

